/* ... Other Styles ... */

.ordering-section h2 {
    position: relative;
    display: inline-block; /* Makes the h2 width only as wide as the text */
}

.ordering-section h2:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -10px; /* Adjust as needed */
    left: 0;
    height: 20px; /* Adjust as needed */
    width: 100%;
    background: linear-gradient(45deg, #EF8B17 25%, transparent 25%) 0 0, 
                linear-gradient(45deg, #EF8B17 25%, transparent 25%) 14px 0, 
                linear-gradient(-45deg, #EF8B17 25%, transparent 25%) 0 0, 
                linear-gradient(-45deg, #EF8B17 25%, transparent 25%) 14px 0;
    background-size: 21px 21px;
    background-color: transparent;
}

