/* Styling for Testimonials Section */
.testimonials-section {
    background-color: #f8f4dc; /* Whitish/Beige Background */
    padding: 1px;
    text-align: center;
    
}

.testimonials-section h2 {
    color: #daa520; /* Golden Text Color */
    font-size: 2em;
    margin-bottom: 30px;
}

.testimonial {
    display: flex;
    align-items: center;
    justify-content: center; /* Centers the items */
    margin-bottom: 20px;
}

.testimonial-textbox {
    background-color: #fff; /* White Background for Textbox */
    color: #333; /* Dark Text Color */
    padding: 10px;
    border: 5px solid #000000; /* Border Color */
    border-radius: 15px; /* Optional: for rounded corners */
    width: 15%; /* Adjusted Width */
    margin-right: -150px; /* Space between text box and stars */
}

.testimonial p {
    margin-bottom: 10px;
}

.author {
    font-weight: bold;
}

.stars {
    color: #daa520; /* Golden Color for Stars */
    font-size: 2em; /* Larger Font Size for Stars */
    width: 20%; /* Adjusted Width */
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; /* Black Outline */
}

/* ... Other Styles ... */

/* Mobile View Optimization */
@media (max-width: 768px) {
    .testimonial {
        flex-direction: column; /* Stack elements vertically */
        align-items: center; /* Center-align the stacked elements */
    }

    .testimonial-textbox, .stars {
        width: 100%; /* Take the full width */
        margin-right: 0; /* Remove the margin between text box and stars */
        margin-bottom: 10px; /* Add some space between stacked elements */
    }
}
